import './style.css';
import React from 'react';
import AlimentTabItem from '../AlimentTabItem/AlimentTabItem';

function AlimentList(props) {
  return (
    <div>
      <h1>List aliments</h1>
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col">Label</th>
            <th scope="col">kcal/100g</th>
            <th scope="col">kcal/1</th>
          </tr>
        </thead>
        <tbody>
          {
            props.aliments.map(aliment =>
              <AlimentTabItem key={aliment.id} aliment={aliment} onSelected={props.onSelected} />)
          }
        </tbody>
      </table>
    </div>
  );
}

export default AlimentList;



