import StorageProcessor from "./StorageProcessor";
import * as common from './common';

class ConsumptionStorageProcessor extends StorageProcessor {

    /**
     * retrieving all the aliments form the list
     */
    findByCurrentFrenchDate() {
        const datetime = common.dateToFrenchFormat(new Date(Date.now()));
        return this.findByFrenchDatetime(datetime);
    }

    findByFrenchDate(stringDate) {
        const datetime = common.dateToFrenchFormat(new Date(Date.parse(stringDate)));
        return this.findByFrenchDatetime(datetime);
    }

    findByFrenchDatetime(datetime) {
        // separate date and time
        const date = datetime.split('  ')[0];
        const fullList = JSON.parse(localStorage.getItem(this.key));
        return fullList.filter(c => c.datetime.split('  ')[0] === date);
    }

}

export default ConsumptionStorageProcessor;


