import './App.css';
import React, { Component } from 'react';
import IdGenerator from './IdGenerator';
import StorageProcessorFactory from './StorageProcessorFactory';
import ConsumptionModel from './models/ConsumptionModel';
import Menu from './components/Menu/Menu';
import MenuNavLink from './components/MenuNavLink/MenuNavLink';
import MenuTabPane from './components/MenuTabPane/MenuTabPane';
import MenuNavLinks from './components/MenuNavLinks/MenuNavLinks';
import MenuTabPanes from './components/MenuTabPanes/MenuTabPanes';
import AlimentManagerTabPane from './components/AlimentManagerTabPane/AlimentManagerTabPane';
import ConsumptionTabPane from './components/ConsumptionTabPane/ConsumptionTabPane';
import * as common from './common';

class App extends Component {

  constructor(props) {
    super(props);
    this.idGenerator = new IdGenerator();
    this.consumptionSP = new StorageProcessorFactory.getConsumptionSP();
    this.state = {
      navLinks: [
        { id: "consumption", active: true, value: "Consumption" },
        { id: "aliments-manager", active: false, value: "Manage Aliments" },
        { id: "profil", active: false, value: "Profil" },
        { id: "account", active: false, value: "Account" },
      ],
      consumedAliments: this.consumptionSP.findByCurrentFrenchDate(),
    }
  }

  toogleNavLink = linkId => {
    const newLinksState = this.state.navLinks;
    const clickedLink = newLinksState.filter(link => link.id === linkId)[0];
    const index = newLinksState.indexOf(clickedLink);
    // disable all links
    newLinksState.forEach(l => l.active = false);
    // active clicked link
    newLinksState[index].active = true;
    this.setState({
      navLinks: newLinksState
    });
  }

  consumeAliment = alimentId => {
    const consumption = new ConsumptionModel(
      this.idGenerator.generateConsumptionId(),
      alimentId,
      common.dateToFrenchFormat(new Date(Date.now()))
    );
    this.consumptionSP.addToList(consumption);
    const consumedAliments = this.consumptionSP.findAll();
    this.setState({ consumedAliments });
  }

  doFilter = date => {
    const consumedAliments = this.consumptionSP.findByFrenchDate(date);
    this.setState({ consumedAliments })
  }

  render() {
    return (
      <div className="container-fluid">
        <Menu
          navlinks={
            <MenuNavLinks>
              <MenuNavLink linkId={this.state.navLinks[0].id} active={this.state.navLinks[0].active} onClick={this.toogleNavLink}>{this.state.navLinks[0].value}</MenuNavLink>
              <MenuNavLink linkId={this.state.navLinks[1].id} active={this.state.navLinks[1].active} onClick={this.toogleNavLink}>{this.state.navLinks[1].value}</MenuNavLink>
              <MenuNavLink linkId={this.state.navLinks[2].id} active={this.state.navLinks[2].active} onClick={this.toogleNavLink}>{this.state.navLinks[2].value}</MenuNavLink>
              <MenuNavLink linkId={this.state.navLinks[3].id} active={this.state.navLinks[3].active} onClick={this.toogleNavLink}>{this.state.navLinks[3].value}</MenuNavLink>
            </MenuNavLinks>
          }
          tabPanes={
            <MenuTabPanes>
              <ConsumptionTabPane
                tabId={this.state.navLinks[0].id}
                active={this.state.navLinks[0].active}
                consumedAliments={this.state.consumedAliments}
                doFilter={this.doFilter}
              />
              <AlimentManagerTabPane tabId={this.state.navLinks[1].id} active={this.state.navLinks[1].active} onConsume={this.consumeAliment} />
              <MenuTabPane tabId={this.state.navLinks[2].id} active={this.state.navLinks[2].active}>content-2</MenuTabPane>
              <MenuTabPane tabId={this.state.navLinks[3].id} active={this.state.navLinks[3].active}>content-4</MenuTabPane>
            </MenuTabPanes>
          }
        />
      </div>
    );
  }
}

export default App;



