import './style.css';
import React, { Component } from 'react';
import ConsumedList from '../ConsumedList/ConsumedList';
import MenuTabPane from '../MenuTabPane/MenuTabPane';

class ConsumptionTabPane extends Component {

  constructor(props) {
    super(props);
    this.state = {
      searchDate: this.props.searchValueChange
    };
    this.searchByDate = this.searchByDate.bind(this);
    this.searchValueChange = this.searchValueChange.bind(this);
  }

  searchValueChange(event) {
    event.preventDefault();
    const searchDate = event.target.value;
    this.setState({ searchDate });
  }

  searchByDate(event) {
    event.preventDefault();
    this.props.doFilter(this.state.searchDate);
  }

  render() {
    return (
      <MenuTabPane tabId={this.props.tabId} active={this.props.active}>
        <nav className="navbar navbar-light bg-light">
          <a className="navbar-brand">Consuption</a>
          <form className="form-inline">
            <input className="form-control mr-sm-2" type="date" aria-label="Search" value={this.props.filterDateValue} onChange={this.searchValueChange} />
            <button className="btn btn-outline-success my-2 my-sm-0" type="submit" onClick={this.searchByDate}>Go</button>
          </form>
        </nav>
        <ConsumedList consumedAliments={this.props.consumedAliments} />
      </MenuTabPane>
    );
  }
}

export default ConsumptionTabPane;



