import './style.css';
import React from 'react';

function ConsumedTabItem(props) {
  let howMush = props.item.quantity + " unit(s)";
  if (0 === props.item.quantity) {
    howMush = props.item.weight + " g"
  }

  return (
    <tr key={props.item.id} >
      <td>{props.item.alimentId}</td>
      <td>{howMush}</td>
      <td>...kcal</td>
      <td>{props.item.datetime}</td>
    </tr>
  );
}
export default ConsumedTabItem;



