import './style.css';
import React from 'react';

function AlimentTabItem(props) {
  return (
    <tr key={props.aliment.id} onClick={() => props.onSelected(props.aliment.id)}>
      <td>{props.aliment.label}</td>
      <td>{props.aliment.energyPer100g}</td>
      <td>{props.aliment.energyPerUnit}</td>
    </tr>
  );
}
export default AlimentTabItem;



